.qty-wr {
	display: flex;
	align-items: center;
	position: relative;

	.qty-btn {
		position: absolute;
		height: 40%;
		width: 1.5rem;
		display: flex;
		align-items: center;
		justify-content: center;


		&-remove {
			bottom: 0;
			right: 0;

			&::before {
				content: '';
				position: absolute;
				height: 0;
				width: 0;
				border-left: .5rem solid transparent;
				border-right: .5rem solid transparent;
				border-top: .5rem solid $gray-02;
			}

			&:hover {
				&::before {
					border-top-color: $orange;
				}
			}
		}

		&-add {
			top: 0;
			right: 0;

			&::before {
				content: '';
				position: absolute;
				height: 0;
				width: 0;
				border-left: .5rem solid transparent;
				border-right: .5rem solid transparent;
				border-bottom: .5rem solid $gray-02;
			}

			&:hover {
				&::before {
					border-bottom-color: $orange;
				}
			}
		}

		&.disabled {
			pointer-events: none;
			opacity: .5;
		}
	}


	input[type=number] {
		margin-right: 1.8rem;
		text-align: center;
		background: $white;
		border: 1px solid $gray-02;
		font-size: 1.6rem;
		color: $gray-02;
		width: 3rem;
		height: 2.7rem;
		-webkit-appearance: none;
		appearance: none;
		font-family: $avenir;
		padding: 0;
	}

	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	input[type=number] {
		-moz-appearance: textfield;
	}
}