.filter-module {
	background: $white;
	border-radius: .5rem;
	padding: 1.5rem;
	box-shadow: 0 0 1rem rgba($black, .1);

	@include media('>mddesktop') {
		padding: 1.5rem 1.5rem 1.5rem 4.9rem;
	}

	form {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include media('<=desktop') {
			flex-wrap: wrap;
			// align-items: flex-start;
		}
	}

	.form-group {
		display: flex;
		flex-direction: column;
		position: relative;
		margin-right: 1rem;
		margin-bottom: 2rem;
		width: 100%;

		@include media('>=tablet') {
			border-right: 2px solid $gray-03;
			width: auto;
			margin-bottom: 0;
		}

		@include media('>=desktop') {
			margin-right: 2.5rem;
		}

		&.with-icon {
			input {
				padding-left: 2.2rem;
			}
		}

		&.with-dot {
			border: none;

			@include media('>=tablet') {
				&::after {
					content: '';
					height: .3rem;
					width: .3rem;
					background: $gray-02;
					border-radius: 50%;
					position: absolute;
					right: .7rem;
					bottom: 1.1rem;
				}
			}
		}

		&.with-chevron {
			padding-right: 0rem;

			@include media('>=desktop') {
				padding-right: 2rem;
			}

			@include media('>=tablet') {
				&::after {
					content: '';
					border-width: 0 1px 1px 0;
					border-style: solid;
					border-color: $gray-02;
					transform: rotate(45deg);
					height: .7rem;
					width: .7rem;
					position: absolute;
					right: 1rem;
					bottom: 1rem;

					@include media('>=desktop') {
						right: 2.9rem;
					}
				}
			}
		}

		.ico-holder {
			position: absolute;
			bottom: 0;
			left: 0;
		}

		input[type=text] {
			border: none;
			background: transparent;
			color: $dark;
			font-size: 1.4rem;
			font-family: $fira;
			font-weight: 700;
			font-style: italic;
			text-indent: .2rem;
			max-width: 20.5rem;

			@include media('>=mddesktop') {
				font-size: 1.8rem;
			}

			&.datepicker {
				color: $dark-02;
				width: 14rem;

				@include media('<=mddesktop') {
					max-width: 10rem;
				}
			}
		}
	}

	.qty-group {
		display: flex;
		align-items: flex-start;

		@include media('<desktop') {
			// margin-bottom: 2rem;
		}

		.qty-item {
			margin-right: 2rem;

			@include media('>=desktop') {
				margin-right: 2.9rem;
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	.btn {
		margin: 2rem auto 0;

		@include media('>=desktop') {
			margin: 0;
			flex-grow: 1;
		}
	}

	.add-link {
		margin: 1rem 1rem 0;

		@include media('>=tablet') {
			margin: 0 1rem;
		}

		@include media('>=desktop') {
			margin: 0 2.2rem;
		}
	}
}