@font-face {
	font-family: 'Proxima Nova';
	src: url('#{$fonts}subset-ProximaNova-Regular.woff2') format('woff2'),
	url('#{$fonts}subset-ProximaNova-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Proxima Nova';
	src: url('#{$fonts}subset-ProximaNova-Bold.woff2') format('woff2'),
	url('#{$fonts}subset-ProximaNova-Bold.woff') format('woff');
	font-weight: 700;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Fira Sans';
	src: url('#{$fonts}subset-FiraSans-BoldItalic.woff2') format('woff2'),
	url('#{$fonts}subset-FiraSans-BoldItalic.woff') format('woff');
	font-weight: 700;
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Avenir Next Cyr';
	src: url('#{$fonts}AvenirNextCyr-Regular.woff2') format('woff2'),
		url('#{$fonts}AvenirNextCyr-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}