// vars
$images: '../images/';
$fonts: '../fonts/';

// fonts
$proxima: 'Proxima Nova', sans-serif;
$fira: 'Fira Sans', sans-serif;
$avenir: 'Avenir Next Cyr', sans-serif;

$breakpoints: (
	phone: 576px,
	tablet: 768px,
	desktop: 992px,
	minscroll: 1025px,
	mddesktop: 1200px,
	xldesktop: 1680px
);

// colors
$white: #fff;
$black: #000;
$dark: #232323;
$dark-02: #383838;
$gray: #6b6b6b;
$gray-02: #9a9a9a;
$gray-03: #e4e4e4;
$orange: #ed9915;

// base vars
$body-bg: $white;
$body-color: $dark;
$font-family-base: $proxima;
$font-size-base: 1.2rem;
$line-height-base: 1.5;
$links-color-base: inherit;
$headings-font-weight: 700;

$transition: all .3s ease;