@font-face {
	font-family: 'Avenir Next Cyr';
	src: url("../fonts/AvenirNextCyr-Regular.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

html {
	font-size: 10px;
}

body {
	margin: 0;
	min-width: 320px;
	background: #fff;
	color: #232323;
	font: 1.2rem/1.5 "Proxima Nova", sans-serif;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	text-decoration: underline;
}

button,
input[type="search"],
input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

#main {
	flex-grow: 1;
	padding: 12.2rem 0;
}

h1 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h1 a, h1 a:hover {
	color: inherit;
}

h2 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h2 a, h2 a:hover {
	color: inherit;
}

h3 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h3 a, h3 a:hover {
	color: inherit;
}

h4 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h4 a, h4 a:hover {
	color: inherit;
}

h5 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h5 a, h5 a:hover {
	color: inherit;
}

h6 {
	margin: 0 0 0.5em;
	font-weight: 700;
}

h6 a, h6 a:hover {
	color: inherit;
}

figure {
	margin: 0;
}

.container {
	margin: 0 auto;
	max-width: 1170px;
	padding: 0 1.5rem;
}

/* buttons */
.btn {
	white-space: normal;
}

.btn .fa {
	margin-left: 5px;
}

.filter-module {
	background: #fff;
	border-radius: .5rem;
	padding: 1.5rem;
	box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
}

@media (min-width: 1201px) {
	.filter-module {
		padding: 1.5rem 1.5rem 1.5rem 4.9rem;
	}
}

.filter-module form {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

@media (max-width: 992px) {
	.filter-module form {
		flex-wrap: wrap;
	}
}

.filter-module .form-group {
	display: flex;
	flex-direction: column;
	position: relative;
	margin-right: 1rem;
	margin-bottom: 2rem;
	width: 100%;
}

@media (min-width: 768px) {
	.filter-module .form-group {
		border-right: 2px solid #e4e4e4;
		width: auto;
		margin-bottom: 0;
	}
}

@media (min-width: 992px) {
	.filter-module .form-group {
		margin-right: 2.5rem;
	}
}

.filter-module .form-group.with-icon input {
	padding-left: 2.2rem;
}

.filter-module .form-group.with-dot {
	border: none;
}

@media (min-width: 768px) {
	.filter-module .form-group.with-dot::after {
		content: '';
		height: .3rem;
		width: .3rem;
		background: #9a9a9a;
		border-radius: 50%;
		position: absolute;
		right: .7rem;
		bottom: 1.1rem;
	}
}

.filter-module .form-group.with-chevron {
	padding-right: 0rem;
}

@media (min-width: 992px) {
	.filter-module .form-group.with-chevron {
		padding-right: 2rem;
	}
}

@media (min-width: 768px) {
	.filter-module .form-group.with-chevron::after {
		content: '';
		border-width: 0 1px 1px 0;
		border-style: solid;
		border-color: #9a9a9a;
		transform: rotate(45deg);
		height: .7rem;
		width: .7rem;
		position: absolute;
		right: 1rem;
		bottom: 1rem;
	}
}

@media (min-width: 768px) and (min-width: 992px) {
	.filter-module .form-group.with-chevron::after {
		right: 2.9rem;
	}
}

.filter-module .form-group .ico-holder {
	position: absolute;
	bottom: 0;
	left: 0;
}

.filter-module .form-group input[type=text] {
	border: none;
	background: transparent;
	color: #232323;
	font-size: 1.4rem;
	font-family: "Fira Sans", sans-serif;
	font-weight: 700;
	font-style: italic;
	text-indent: .2rem;
	max-width: 20.5rem;
}

@media (min-width: 1200px) {
	.filter-module .form-group input[type=text] {
		font-size: 1.8rem;
	}
}

.filter-module .form-group input[type=text].datepicker {
	color: #383838;
	width: 14rem;
}

@media (max-width: 1200px) {
	.filter-module .form-group input[type=text].datepicker {
		max-width: 10rem;
	}
}

.filter-module .qty-group {
	display: flex;
	align-items: flex-start;
}

.filter-module .qty-group .qty-item {
	margin-right: 2rem;
}

@media (min-width: 992px) {
	.filter-module .qty-group .qty-item {
		margin-right: 2.9rem;
	}
}

.filter-module .qty-group .qty-item:last-child {
	margin-right: 0;
}

.filter-module .btn {
	margin: 2rem auto 0;
}

@media (min-width: 992px) {
	.filter-module .btn {
		margin: 0;
		flex-grow: 1;
	}
}

.filter-module .add-link {
	margin: 1rem 1rem 0;
}

@media (min-width: 768px) {
	.filter-module .add-link {
		margin: 0 1rem;
	}
}

@media (min-width: 992px) {
	.filter-module .add-link {
		margin: 0 2.2rem;
	}
}

.btn {
	background: #ed9915;
	font-size: 1.6rem;
	font-weight: 700;
	text-transform: uppercase;
	border-radius: .2rem;
	height: 6.3rem;
	padding: 2rem 3rem;
	border: none;
	color: #fff;
}

.qty-wr {
	display: flex;
	align-items: center;
	position: relative;
}

.qty-wr .qty-btn {
	position: absolute;
	height: 40%;
	width: 1.5rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.qty-wr .qty-btn-remove {
	bottom: 0;
	right: 0;
}

.qty-wr .qty-btn-remove::before {
	content: '';
	position: absolute;
	height: 0;
	width: 0;
	border-left: .5rem solid transparent;
	border-right: .5rem solid transparent;
	border-top: 0.5rem solid #9a9a9a;
}

.qty-wr .qty-btn-remove:hover::before {
	border-top-color: #ed9915;
}

.qty-wr .qty-btn-add {
	top: 0;
	right: 0;
}

.qty-wr .qty-btn-add::before {
	content: '';
	position: absolute;
	height: 0;
	width: 0;
	border-left: .5rem solid transparent;
	border-right: .5rem solid transparent;
	border-bottom: 0.5rem solid #9a9a9a;
}

.qty-wr .qty-btn-add:hover::before {
	border-bottom-color: #ed9915;
}

.qty-wr .qty-btn.disabled {
	pointer-events: none;
	opacity: .5;
}

.qty-wr input[type=number] {
	margin-right: 1.8rem;
	text-align: center;
	background: #fff;
	border: 1px solid #9a9a9a;
	font-size: 1.6rem;
	color: #9a9a9a;
	width: 3rem;
	height: 2.7rem;
	-webkit-appearance: none;
	appearance: none;
	font-family: "Avenir Next Cyr", sans-serif;
	padding: 0;
}

.qty-wr input::-webkit-outer-spin-button,
.qty-wr input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.qty-wr input[type=number] {
	-moz-appearance: textfield;
}

.add-link {
	border-radius: 50%;
	background: #ed9915;
	opacity: .58;
	color: #fff;
	font-size: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 3.5rem;
	width: 3.5rem;
	line-height: 1;
	flex-shrink: 0;
}

.add-link:hover {
	text-decoration: none;
}
